* {
  font-family:Verdana, Geneva, Tahoma, sans-serif;
}

body{
  padding: 40px;
}

input {
  color: grey;
  font-size: 1.3rem;
  width: 60%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card img {
  background-size: contain;
}

.card p {
  padding: 5px;
  text-align: center;
}